import React, { ReactNode, useMemo } from 'react';
import Header from 'components/Header';
import Footer from 'components/Footer';
import { Container, Grid, makeStyles, Theme } from '@material-ui/core';

interface Props {
  header?: boolean;
  footer?: boolean;
  left?: ReactNode;
  right?: ReactNode;
  rightSticky?: boolean;
  leftSticky?: boolean;
  children: ReactNode;
}

const useStyles = makeStyles<Theme>(({ breakpoints, spacing }) => ({
  container: {
    padding: `${spacing(3)}px ${spacing(2)}px`,
    [breakpoints.up('lg')]: {
      padding: `${spacing(6)}px ${spacing(4)}px`,
    },
  },
  left: {
    [breakpoints.down('md')]: {
      order: 2,
    },
  },
  center: {
    [breakpoints.down('md')]: {
      order: 1,
    },
  },
  right: {
    [breakpoints.down('md')]: {
      order: 3,
    },
  },
  sticky: {
    alignSelf: 'flex-start',
    position: 'sticky',
    top: spacing(2),
  },
}));

const Layout = ({
  header = true,
  footer = true,
  children,
  left,
  right,
  leftSticky = false,
  rightSticky = false,
}: Props) => {
  const styles = useStyles();

  const desktopCenterGridSize = useMemo(() => {
    if (left && right) {
      return 6;
    } else if (left || right) {
      return 8;
    } else {
      return 12;
    }
  }, [left, right]);

  return (
    <div>
      {header && <Header />}
      <Container component="main" className={styles.container}>
        <Grid container spacing={2}>
          {left && (
            <Grid item xs={12} lg={right ? 3 : 4} className={`${styles.left} ${leftSticky && styles.sticky}`}>
              {left}
            </Grid>
          )}
          <Grid item xs={12} lg={desktopCenterGridSize} className={styles.center}>
            {children}
          </Grid>
          {right && (
            <Grid item xs={12} lg={left ? 3 : 4} className={`${styles.right} ${rightSticky && styles.sticky}`}>
              {right}
            </Grid>
          )}
        </Grid>
      </Container>
      {footer && <Footer />}
    </div>
  );
};

export default Layout;
