import React, { useCallback } from 'react';
import { Box, Container, Grid, makeStyles, Theme, Button } from '@material-ui/core';
import { useStaticQuery, graphql, Link } from 'gatsby';
import Image from 'gatsby-image';
import { GlobalMenus } from 'models/menu';
import { linkEvent } from 'utils/loggers';
import * as Icon from 'react-feather';

const useStyles = makeStyles<Theme>(theme => ({
  root: {
    padding: theme.spacing(2),
    backgroundColor: theme.palette.background.paper,
  },
  wrapper: {
    justifyContent: 'center',
    [theme.breakpoints.up('lg')]: {
      justifyContent: 'flex-start',
    },
  },
  logo: {
    marginBottom: theme.spacing(2),
    textAlign: 'center',
    [theme.breakpoints.up('lg')]: {
      marginBottom: 0,
      marginTop: 4,
      marginRight: theme.spacing(3),
    },
  },
  list: {
    margin: 0,
    padding: 0,
  },
  item: {
    margin: `0 ${theme.spacing(2)}px`,
    fontSize: theme.typography.caption.fontSize,
    listStyle: 'none',
    [theme.breakpoints.up('lg')]: {
      marginLeft: 0,
      marginRight: theme.spacing(4),
    },
  },
  link: {
    textDecoration: 'none',
    color: 'inherit',
    '&.active': {
      color: theme.palette.primary.main,
    },
    '&:visited': {
      color: 'inherit',
    },
  },
  searchButtonArea: {
    '& a': {
      lineHeight: 0,
    },
    [theme.breakpoints.down('md')]: {
      display: 'none',
    },
  },
}));

const handleLogoClick = () => linkEvent('/', '   ');

const handleMenuClick = (url: string) => linkEvent(url, '   ');

const handleSearchClick = () => linkEvent('/search', '   ');

const Header = () => {
  const styles = useStyles();

  const data = useStaticQuery(graphql`
    query HeaderQuery {
      logo: file(absolutePath: { regex: "/logo-text.png/" }) {
        childImageSharp {
          fixed(width: 100, height: 24) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `);
  const {
    logo: {
      childImageSharp: { fixed: logoUrl },
    },
  } = data;

  return (
    <Box component="header" boxShadow={3} className={styles.root}>
      <Container>
        <Grid container spacing={0} alignItems="center" justify="space-between">
          <Grid container item xs={12} md={10} className={styles.wrapper} alignItems="center">
            <Grid item xs={12} lg={'auto'} className={styles.logo}>
              <Link to="/" onClick={handleLogoClick}>
                <Image fixed={logoUrl} alt="" />
              </Link>
            </Grid>
            <Grid item>
              <Box component="ul" display="flex" className={styles.list}>
                {GlobalMenus.map(menu => (
                  <Box key={menu.path} component="li" className={styles.item}>
                    <Link
                      activeClassName="active"
                      to={menu.path}
                      onClick={() => handleMenuClick(menu.path)}
                      className={styles.link}
                    >
                      {menu.name}
                    </Link>
                  </Box>
                ))}
              </Box>
            </Grid>
          </Grid>
          <Grid container item lg={2} alignItems="center" justify="flex-end" className={styles.searchButtonArea}>
            <Button onClick={handleSearchClick}>
              <Link to="/search">
                <Icon.Search />
              </Link>
            </Button>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default Header;
