import React, { useMemo } from 'react';
import { Box, Container, Grid, Typography, makeStyles, Theme } from '@material-ui/core';
import { useStaticQuery, graphql, Link } from 'gatsby';
import Image from 'gatsby-image';

const useStyles = makeStyles<Theme>(theme => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(4),
    paddingBottom: theme.spacing(10),
  },
  logo: {
    display: 'block',
    marginBottom: theme.spacing(1),
  },
  inlineLink: {
    display: theme.palette.primary.main,
    textDecoration: 'none',
  },
}));

const Footer = () => {
  const styles = useStyles();
  const data = useStaticQuery(graphql`
    query FooterQuery {
      logo: file(absolutePath: { regex: "/logo-text.png/" }) {
        childImageSharp {
          fixed(width: 100, height: 24) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      site {
        siteMetadata {
          author {
            name
          }
        }
      }
    }
  `);
  const {
    logo: {
      childImageSharp: { fixed: logoUrl },
    },
    site: {
      siteMetadata: { author },
    },
  } = data;
  const thisYear = useMemo(() => new Date().getFullYear(), []);

  return (
    <Box component="footer" className={styles.root}>
      <Container>
        <Grid container spacing={0} alignItems="center">
          <Grid>
            <Link to="/" className={styles.logo}>
              <Image fixed={logoUrl} alt="" />
            </Link>
            <Typography variant="caption" component="p" color="textSecondary">
               {thisYear} {author.name} Powered by{' '}
              <a href="https://www.gatsbyjs.org/" className={styles.inlineLink}>
                Gatsby
              </a>
            </Typography>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default Footer;
